var tha,js;
export default {
	onLoad(){
		tha = this;
		
	},
	onUnload(){
		clearInterval(js)
		this.second = 0;
	},
	data() {
		return {
			phoneno:'',
			password:'',
			code:'',
			xieyi:true,
			showPassword:false,
			second:0
		};
	},
	computed:{
		yanzhengma(){
			if(this.second==0){
				return '获取验证码';
			}else{
				if(this.second<10){
					return '重新获取0'+this.second;
				}else{
					return '重新获取'+this.second;
				}
			}
		}
	},
	methods: {
		display() {
		    this.showPassword = !this.showPassword
		},
		
		xieyitong(){
			this.xieyi = !this.xieyi;
		},
		
		getcode(){
			if (!this.phoneno) {
			uni.showToast({
			    icon: 'none',
			    title: '手机号不能为空'
			});
			return;
			}
			if (this.phoneno.length !=11) {
			    uni.showToast({
			        icon: 'none',
			        title: '手机号不正确'
			    });
			    return;
			}
			if(this.second>0){
				return;
			}
			this.second = 60;
			this.$api.sendMsg({mobile: this.phoneno}).then(res => {
			   let phone = this.phoneno.replace(/^(\d{3})\d{4}(\d+)/,"$1****$2");
			   uni.showToast({icon: 'none', title: `验证码已发送至${phone}`});
			   js = setInterval(() =>{
			   	this.second--;
			   	if(this.second==0){
			   		clearInterval(js)
			   	}
			   },1000)
			}).catch(err => {
			})
		},
		
	    bindLogin() {
			if (this.xieyi == false) {
			    uni.showToast({
			        icon: 'none',
			        title: '请先阅读《软件用户协议》'
			    });
			    return;
			}
			if (this.phoneno.length !=11) {
			    uni.showToast({
			        icon: 'none',
			        title: '手机号不正确'
			    });
			    return;
			}
	        if (this.password.length < 6 || this.password.length > 16) {
	            uni.showToast({
	                icon: 'none',
	                title: '请输入6到16位密码'
	            });
	            return;
	        }
			if (this.code.length != 6) {
			    uni.showToast({
			        icon: 'none',
			        title: '验证码不正确'
			    });
			    return;
			}
			this.$api.register({
				mobile: this.phoneno,
				password:this.password,
				code:this.code,
			}).then(res => {
				uni.showToast({
				    icon: 'none',
				    title: '注册成功'
				});
				this.$api.login({
					mobile:this.phoneno,
					password:this.password
				}).then(res => {
					uni.navigateTo({
						url: `/pages/user/personal`
					})
				}).catch(err => {
				})
			}).catch(err => {
			})
			
	    }
	}
}
